@import url(https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Rubik";
}

:root {
    --Light-grey: #595151;
    --Blue-accent: #2096F3;
}

body {
    background: #2096F3;
    background: var(--Blue-accent);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.loading-wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 0.25em;
    gap: 0.25em;
    /* height: 70px; */
    min-height: 40vh;
}


.loading-wrapper > div{
    width: 12px;
    height: 70px;
    border-radius: 40px;
    background: #000;
    animation: bar 1s linear  infinite  ;
}

.loading-wrapper > div:nth-child(even){
    animation-delay: 0.4s;
}

@keyframes bar {
    0%{
        height: 30px;
    }
    50%{
        height: 50px;
    }
    100%{
        height: 30px;
    }
}
.wrapper{
    background: #fff;
    padding: 3em;
    height: 100%;
    border-radius: 4em;
}

/* Card Top */

.card-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.profile-picture{
    width: 120px;
    object-fit: cover;
    border-radius: 50%;
    border: 2px solid #000;
}

.profile-button:hover{
    background: rgb(141, 137, 137);
    color: rgb(255, 255, 255);
    border: none;
}

.profile-button{
    border-radius: 30px;
    padding: 0.7em 1.4em;
    border: 2px solid #EAEAEA;
    background: transparent;
    font-size: 1.075rem;
    font-weight: 500;
    cursor: pointer;
    color: rgb(88, 85, 85);
}

/* Mid */
.name{
    font-size: 1.35rem;
    font-weight: 500;
    margin-block: 0.9em 0.25em;
}

.username{
    font-size: 1.1rem;
    color: #7a7575;
    font-weight: 500;
    margin-bottom: 1em;
}

/* Footer */
.tagline{
    font-size: 0.9rem;
}

.heading {
    font-size: 4rem;
    text-align: center;
    margin-block: 1em;
    font-family: 'Arvo' !important;
}
.container {
    width: 80%;
    margin: auto;
    max-width: 1440px;
}

.profile-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-gap: 2em;
    gap: 2em;
    height: 72vh;
    overflow-y: auto;

    padding-right: 0.5em;

    /* Scroll bar Mozilla */
    scrollbar-width: thin;
}

/* Chrome + other browsers */
.profile-container::-webkit-scrollbar{
    width: 0.25vw;
}
.profile-container::-webkit-scrollbar-thumb{
    background: rgb(105, 104, 104);
}
.profile-container::-webkit-scrollbar-track{
    background: rgb(235, 232, 232);
}


.address-content > * , 
.address-header > *{
    font-family: 'Nunito';
}

/* Address Header */
.address-header{
    display: flex;
    align-items: center;
    grid-gap: 0.25em;
    gap: 0.25em;
}
.address-header span{
    font-size: 0.87rem;
    font-weight: 700;
    color: #a5adb9;
}
.address-header div{
    background: #a5adb9;
    height: 2px;
    width: 100%;
}

/* Address content */
.city{
    font-weight: 800;
    font-size: 1.8rem;
    margin-block: 0.5em 0.3em;
    color: #595151;
}
.street-suite , .zip{
    color: #a5adb9;
    font-weight: 700;
    font-size: 0.95rem;
}
/* Font Family */
.company-header > *,
.company-content > *{
    font-family: 'Nunito';
}

/* Address Header */
.company-header{
    display: flex;
    align-items: center;
    grid-gap: 0.25em;
    gap: 0.25em;
}
.company-header span{
    font-size: 0.87rem;
    font-weight: 700;
    color: #a5adb9;
}
.company-header div{
    background: #a5adb9;
    height: 2px;
    width: 100%;
}

/* Company Content */

.company-content p:nth-of-type(1){
    margin-top: 1.2em;
}

.company-content p{
    color: #595151;
    font-weight: 700;
    font-size: 1.2rem;
}

/* Actual Image */
.profile-image{
    width: 230px;
    border-radius: 8px;
    object-fit: cover;
}

/* Fonts */
.user-info-wrapper .name,
.user-info-wrapper .username,
.user-info-wrapper .coordinates,
.email-box > *{
    font-family: 'Nunito';
}

/* Header */
.user-info-header{
    display: flex;
    align-items: flex-end;
    grid-gap: 1.5em;
    gap: 1.5em;
}
.user-info-header div{
    display: flex;
    align-items: center;
}

.location-icon{
    width: 30px;
}

.user-info-wrapper .name{
    font-weight: 900;
    font-size: 3rem;
    margin-bottom: -8px;
    color: var(--Light-grey);
}

.email-box {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: 1.6em;
    max-width: 600px;
}

.send-back{
    background-color: var(--Blue-accent);
    padding: 0.6em;
    font-size: 1.3rem;
    border: none;
    color: #f2f2f2;
    font-weight: 600;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color .5s;
}

.send-back:hover{
    background: #005faa;
}

.email-box h2{
    font-size: 1.9rem;
    font-weight: 700;
    color: var(--Light-grey);
}

.email-box p{
    color: var(--Blue-accent);
    font-weight: 600;
    font-size: 1.2rem;
}

.coordinates{
    font-weight: 600;
}

.user-info-wrapper .username{
    color: var(--Blue-accent);
    font-weight: 800;
    font-size: 1.2rem;
}
.profile-page-wrapper{
    min-height: 100vh;
    background-color: var(--Blue-accent);
    display: flex;
    align-items: center;
}

.profile-page-wrapper .container{
    background: #fff;
    border-radius: 10px;
    padding: 3em;
}


/* Grid system */
.profile-page-wrapper .container{
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-gap: 2em;
    gap: 2em;
}

